/* Updated responsive layout /
.App {
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 0.9vh 5vw 10vh 5vw;
  gap: 2em;
}
/ Responsive intro section /
.intro-box {
  font-family: 'Source Code Pro', monospace;
  font-size: 16px;
  max-width: 800px;
  width: 100%;
  text-align: left;
}
/ Responsive projects section /
.project-holder {
  font-family: 'Source Code Pro', monospace;
  font-size: 15px;
  max-width: 800px;
  width: 100%;
}
/ Keep all your existing project styles /
.project-category{
  font-family: 'Source Code Pro', monospace;
}
.project{
  font-family: 'Source Code Pro', monospace;
  color: black;
  position: relative;
  display: block;
  width: max-content;
}
.outer {
  display: block;
  color: black;
  background-color: black;
  width: max-content;
}
.project-bg {
  width: 0%;
  transition: all ease 300ms;
  background: 
#ee0979;
  background: -webkit-linear-gradient(to right, 
#ff6a00, 
#ee0979);
  background: linear-gradient(to right, 
#ff6a00, 
#ee0979);
}
.inner {
  display: block;
  color: black;
  width: max-content;
  background-color: transparent;  / Changed from white to transparent /
  mix-blend-mode: screen;
}
.outer:hover .project-bg{
  color: black;
  width: 100%;
}
.project::after {
  content: '';
  position: absolute;
  width: 0%;
  background: 
#ee0979;
  background: -webkit-linear-gradient(to right, 
#ff6a00, 
#ee0979);
  background: linear-gradient(to right, 
#ff6a00, 
#ee0979);
  transition: all ease 300ms;
  height: 1px;
  bottom: 0%;
}
.project:hover::after {
  width: 100%;
}
/ Mobile adjustments */
@media (max-width: 768px) {
  .App {
    padding: 10vh 8vw 5vh 8vw;
  }

  .intro-box {
    font-size: 14px;
  }

  .project-holder {
    font-size: 13px;
  }
}
